@import "variables.scss";

.video {
  width: 100%;
  position: relative;
  text-transform: uppercase;

  @media (min-width: 768px) {
    margin-bottom: 36px;
  }

  @media (min-width: 1200px) {
    margin-bottom: 80px;
  }
}

.video__header {
  padding: 0 26px 26px;

  & > .title {
    font-size: 18px;
    margin-bottom: 1.5em;
  }

  @media (min-width: 768px) {
    padding: 0 125px 82px;

    & > .title {
      font-size: 30px;
      margin-bottom: 1em;
    }
  }

  @media (min-width: 1200px) {
    padding: 3px 340px 79px;
  }
}

.video__wrapper {
  width: 100%;
  position: relative;
  margin-bottom: 60px;

  @media (min-width: 768px) {
    width: 648px;

    margin: 0 auto;
  }

  @media (min-width: 1200px) {
    width: 800px;

    margin: 0 auto;
  }
}

.video__poster {
  width: 100%;
}

.video__controls {
  display: grid;
  grid-template-rows: 45px 65px;
  grid-template-columns: repeat(4, 1fr);
  position: absolute;
  bottom: -65px;


  @media (min-width: 768px) {
    grid-template-rows: 95px;
    grid-template-columns: repeat(4, auto);
    bottom: 0;
  }

  @media (min-width: 1200px) {
    grid-template-rows: 95px;
    grid-template-columns: repeat(4, auto);
  }
}

.video__seek-bar {
  grid-area: 1/1/2/5;
  height: 45px;
  background: $black;
  opacity: 0.6;
  position: relative;
  border: 0;


  @media (min-width: 768px) {
    grid-area: 1/2/2/3;
    height: 95px;
  }

  @media (min-width: 1200px) {
    height: 95px;
  }
}


.video__icon-volume-slider {
  display: block;
  width: 11px;
  height: 11px;
  position: absolute;
  top: 44%;
  left: 31%;

  @media (min-width: 768px) {
    top: 40%;
    left: 31%;
  }

  @media (min-width: 1200px) {
    top: 45%;
    left: 22%;
  }
}

.video__input {
  vertical-align: bottom;
  width: 75%;
}

.video__reload {
  grid-area: 2/1/3/2;
  height: 65px;
  background: $form-input;
  border: none;

  @media (min-width: 768px) {
    grid-area: 1/1/2/2;
    height: 95px;
    background: $black;
    opacity: 0.6;
  }

  @media (min-width: 1200px) {
    height: 95px;
  }
}

.video__empty {
  grid-area: 2/2/3/3;
  height: 65px;
  background: $form-input;
  border: none;

  @media (min-width: 768px) {
    display: none;
  }
}

.video__subtitles {
  grid-area: 2/3/3/4;
  height: 65px;
  background: $form-input;
  border: none;

  @media (min-width: 768px) {
    grid-area: 1/3/2/4;
    height: 95px;
    background: $black;
    opacity: 0.6;
  }

  @media (min-width: 1200px) {
    height: 95px;
  }
}

.video__full-screen {
  grid-area: 2/4/3/5;
  height: 65px;
  background: $form-input;
  border: none;


  @media (min-width: 768px) {
    grid-area: 1/4/2/5;
    height: 95px;
    background: $black;
    opacity: 0.6;
  }

  @media (min-width: 1200px) {
    height: 95px;
  }
}

.video__icon-seek-bar {
  @media (max-width: 1199px) {
    display: block;
  }

  @media (min-width: 1200px) {
    display: none;
  }
}

.video__icon-seek-bar-long {
  @media (max-width: 1199px) {
    display: none;
  }

  @media (min-width: 1200px) {
    display: block;
    width: 540px;
    height: 95px;
  }
}


.video__icon--reload {
  width: 32px;
  height: 20px;
  fill: $video_icon;

  &:hover {
    fill: $form-font-color;
  }

  &:active {
    fill: $form-font-color;
    opacity: 0.3;
  }

  @media (min-width: 768px) {
    width: 82px;
    height: 20px;
    fill: $white;
  }
}

.video__icon--subtitles {
  width: 32px;
  height: 20px;
  fill: $video_icon;

  &:hover {
    fill: $form-font-color;
  }

  &:active {
    fill: $form-font-color;
    opacity: 0.3;
  }

  @media (min-width: 768px) {
    width: 82px;
    height: 20px;
    fill: $white;
  }
}

.video__icon--full-screen {
  width: 32px;
  height: 20px;
  fill: $video_icon;

  &:hover {
    fill: $form-font-color;
  }

  &:active {
    fill: $form-font-color;
    opacity: 0.3;
  }

  @media (min-width: 768px) {
    width: 82px;
    height: 20px;
    fill: $white;
  }
}
